import { Component, OnInit } from '@angular/core';
import { UserUsersService, UserGroupService } from 'src/app/sdk/user/services';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { Pegawai } from 'src/app/sdk/hr/models';
import { Users, Group } from 'src/app/sdk/user/models';


@Component({
    selector: 'app-view',
    templateUrl: './view.component.html',
    styleUrls: ['./view.component.scss']
})
export class ViewComponent implements OnInit {

    constructor(
        private userUsersService: UserUsersService,
        private userGroupService: UserGroupService,
        private direktoriPegawaiService: DirektoriPegawaiService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
    ) { }



    dataUser_cek: any[] = []
    pegawai: Pegawai = {}
    dataUser: Users = {}
    dataGroup: Group = {}

    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;

            this.getUser();
        }
        )


    }


    getUser() {
        this.spinner.show()
        this.userUsersService.getUserUsers(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.dataUser_cek = data.data

                this.dataUser_cek.forEach((item, index) => {
                    // panggie cuti prengaturan
                    this.userUsersService.getUserUsersId(item.id).subscribe(
                        dataRes => {
                            this.dataUser = dataRes.data;
                            this.getPegawai(this.dataUser.id_pegawai);
                            this.getGroup(this.dataUser.id_group);

                        })

                    this.spinner.hide()
                })
                this.spinner.hide()
            }
        )
    }

    getUserPegawai() {
        this.userUsersService.getUserUsersId
    }



    getPegawai(id_pegawai: string) {
        this.direktoriPegawaiService.getDirektoriPegawaisId(id_pegawai).subscribe(
            response => {
                this.pegawai = response.data
            }
        )
    }

    getGroup(id_group: string) {
        this.userGroupService.getUserGroupsId(id_group).subscribe(
            response => {
                this.dataGroup = response.data
            }
        )
    }


    // goToBack() {
    //     this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan', 'user', 'view'])
    // }

}
