import { Component, OnInit } from '@angular/core';
import { UserUsersService, UserGroupService } from 'src/app/sdk/user/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Users, Group } from 'src/app/sdk/user/models';
import { FormControl, Validators } from '@angular/forms';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { DirektoriPegawaiService } from 'src/app/sdk/hr/services';
import { Pegawai } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(

        private userUsersService: UserUsersService,
        private userGroupService: UserGroupService,
        private spinner: NgxSpinnerService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private direktoriPegawaiService: DirektoriPegawaiService,

    ) { }

    dataUser: Users = {}
    dataPegawai: Pegawai = {}
    listGroup: Group [] = []
    id_pegawai: string = ''

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data;
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })

        this.form()
        this.getGroup()
        this.getPegawai()
    }

    getGroup() {
        this.userGroupService.getUserGroups().subscribe(
            data=> {
                this.listGroup =data.data
            }
        )
    }


    getPegawai() {
        this.direktoriPegawaiService.getDirektoriPegawaisId(this.id_pegawai).subscribe(
            data=> {
                this.dataPegawai =data.data
                this.dataForm.patchValue({name : this.dataPegawai.nama_depan + ' ' + this.dataPegawai.nama_belakang})
            }
        )
    }


    dataForm: FormGroupTypeSafe<Users>
    form() {
        this.dataForm = this.fb.group<Users>({
            email: new FormControl('',[Validators.required]),
            id_group: new FormControl('',[Validators.required]),
            password: new FormControl('',[Validators.required]),
            id_pegawai: new FormControl(this.id_pegawai),
            name: new FormControl(''),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.dataUser = this.dataForm.value
        this.userUsersService
            .postUserUsers(this.dataUser)
            .subscribe(
                data => {

                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/pegawai-detail', this.id_pegawai, 'pengaturan', 'user', 'view'])
    }

}
